.header {
    color: white;
}

.header__breadcrumbs {
    font-family: "Charter", serif;
    position: absolute;
    top: pxToVw(45);
    left: pxToVw(272);
    font-size: pxToVw(12.8);
    &:hover{
        .header__submenu{
            opacity: 1;
            pointer-events: all;
        }
    }
}

.header__submenu{
    position: absolute;
    top: -3vw;
    left: pxToVw(-25);
    width: pxToVw(260);
    background-color: #fff;
    box-shadow: 0px 20px 20px 0px rgba(103, 161, 196, 0.45);
    padding: 3vw 0 1.66667vw 0;
    opacity: 0;
    transition: 0.3s;
    pointer-events: none;
    &-list{
        list-style-type: none;
        margin: 0;
        padding: 0;
    }
    &-header{
        display: flex;
        align-items: center;
        font-size: pxToVW(17);
        color: #005090;
        margin-left: pxToVw(25);
        margin-right: pxToVw(25);
        padding-bottom: pxToVw(14);
        border-bottom: 1px solid #C9DEE9;
        margin-bottom: pxToVw(14);
        strong{
            margin-right: 0.25rem;
        }
        span{
            margin-left: 0.25rem;
        }
    }
}

.header__submenu-item{
    margin-bottom: pxToVw(10);
    padding-left: pxToVw(25);
    &:last-child{
        margin-bottom: 0;
    }
}

.header__submenu-link{
    color: #005090;
    font-size: pxToVw(11);
    font-weight: 500;
    transition: 0.3s;
    &:hover{
        color: #65A2C2;
    }
}

.header__logo {
    position: absolute;
    width: pxToVw(150);
    height: pxToVw(40);
    top: pxToVw(40);
    left: pxToVw(40);
    z-index: 100;
    transition: opacity 1s ease;
}

.header__logo_blue {
    opacity: 0;
}

.header__menu {
    position: fixed;
    width: pxToVw(111);
    height: pxToVw(116);
    top: 50.5%;
    transform: translateY(-50%);
    left: pxToVw(15);
    z-index: 100;
    cursor: pointer;
    transition: opacity 1s ease;
}

.header__menu_blue {
    opacity: 0;
}

.header__menu-important {
    opacity: 1 !important;
}

.header__small-menu {
    position: absolute;
    top: pxToVw(45);
    right: pxToVw(50);
    z-index: 100;

    display: flex;
    align-items: center;
}

.header__lang {
    margin-left: pxToVw(25);
    font-size: pxToVw(10);
    // color: #fff;
    font-weight: 500;

    display: flex;
    align-items: center;
}

.header__lang-item {
    display: block;
    transition: color 0.5s ease-in-out;
    &:not(:last-child) {
        margin-right: pxToVw(7);
    }
    &.active {
        border-radius: pxToVw(6.2);
        background-color: #fff;

        padding-top: pxToVw(6);
        padding-bottom: pxToVw(4);
        padding-left: pxToVw(4);
        padding-right: pxToVw(4);

        margin-top: -2px;

        color: #67A1C4;

        display: flex;
        align-items: center;
        justify-content: center;

        box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.16);
    }
}

.header__link {
    margin: 0 pxToVw(20);
    font-size: pxToVw(13);
    font-weight: 300;
}

.header__menu._mobile {
    display: none;
}

.header__phoneIcon {
    display: none;
}

.header__small-menu-blue {
    right: 7vw;
}

.header__small-menu-blue .header__link {
    color: #67a1c4;
    margin: 0 1vw 0 0;
}

.header__wrapper {
    display: block;
}

@media (max-width: 1024px) {
    .header__phoneIcon {
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        top: 12px;
        right: 20px;
        width: 30px;
        height: 30px;
        z-index: 310;
        img {
            width: 60px;
            height: 60px;
        }
    }
    .header__link {
        margin: 0 10px;
        font-size: 12px;
    }

    .header__logo {
        width: 111px;
        height: 40px;
        top: 5px;
        left: 50%;

        transform: translateX(-50%);

        z-index: 310;

        display: none;
    }

    .header__menu {
        display: none;
    }

    .header__menu._mobile {
        display: block;
        opacity: 1;
        position: static;
        transform: unset;
        margin-left: 20px;
        margin-right: 10px;
        width: 15px;
        height: 15px;

        position: absolute;
        left: 0;
        top: 50%;

        transform: translateY(-50%);
        
    }
    .header__menu._mobile.header__menu-close {
        display: none;
    }

    .header__wrapper.active {
        .header__menu._mobile.header__menu-open {
            display: none;
        }
        .header__menu._mobile.header__menu-close {
            display: block;
        }
    }

    .header__small-menu {
        background: white;
        color: #67a1c4;
        position: fixed;
        top: unset;
        left: 5%;
        right: 5%;
        border-radius: 25px;
        display: flex;
        height: 40px;
        align-items: center;
        bottom: 25px;

        display: none;
    }

    .header__breadcrumbs {
        display: none;
    }

    .page404 .header__logo_blue {
        opacity: 1 !important;
    }

    .header {
        &__wrapper {
            height: 55px;
            width: 100%;
            background-color: #005090;
            box-shadow: 0px 0px 10px rgba(0, 80, 146, 0.45);

            position: fixed;
            top: 0;
            left: 0;

            display: flex;
            align-items: center;


            z-index: 605;

            border-bottom-left-radius: 15px;
            border-bottom-right-radius: 15px;

            .header__logo {
                display: block;
            }
        }
    }
}

@media (min-width: 1600px) {


    .header__breadcrumbs {
        top: pxToVw2(45);
        left: pxToVw2(272);
        font-size: pxToVw2(12.8);
    }

    .header__logo {
        width: pxToVw2(150);
        height: pxToVw2(40);
        top: pxToVw2(40);
        left: pxToVw2(40);
    }

    .header__menu {
        width: pxToVw2(111);
        height: pxToVw2(116);
        top: 50.5%;
        transform: translateY(-50%);
        left: pxToVw(15);
    }

    .header__small-menu {
        top: pxToVw2(45);
        right: pxToVw2(50);
    }

    .header__link {
        font-size: pxToVw(13);
    }

    .header__small-menu-blue {
        right: 7.5vw;
    }
}



