* {
  box-sizing: border-box;
}

html {
  font-family: "Museo Sans", sans-serif;
  font-weight: 500;
}

a {
  text-decoration: none;
  color: inherit;
}

p {
  margin: 0;
}

h1,
h2 {
  all: unset;
}
@keyframes fadeInMenu {
  0% {
    opacity: 0;
    -webkit-transform: translateY(30px);
    transform: translateY(30px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0px);
    transform: translateY(0px);
  }
}

@keyframes fadeOutMenu {
  0% {
    opacity: 1;
    -webkit-transform: translateY(0px);
    transform: translateY(0px);
  }
  100% {
    opacity: 0;
    -webkit-transform: translateY(30px);
    transform: translateY(30px);
  }
}

$vwSize: 1500 / 100;

@function pxToVw($px) {
  @return $px / $vwSize + vw;
}

$vwSize2: 1600 / 100;

@function pxToVw2($px) {
  @return $px / $vwSize2 + vw;
}

$vwSize3: 1920 / 100;

@function pxToVw3($px) {
  @return $px / $vwSize3 + vw;
}

.content {
  overflow: hidden;
}

.main {
  height: 100%;
  width: 100%;
  transform: translateY(0px);
  transition: transform 700ms ease 0s;
  color: white;
}

.main_blue {
  color: #005090;
}

.mobile-showed {
  display: none;
}

.content {
  background: #e8f1f6 url(../img/waves4.svg) no-repeat center 70vh / contain;
  background-attachment: fixed;
}

@media (max-width: 1024px) {
  .mobile-showed {
    display: block !important;
  }
  .content {
    background: #e8f1f6 url(../img/waves-mob.svg) no-repeat center bottom/ 100%;
    background-attachment: fixed;
  }

  .mobile-hidden {
    display: none !important;
  }
}

