@media (max-width: 1024px) {
  .content {
    overflow: auto;
  }
}

.hidden {
  display: none;
}

.request-mode [data-request-trigger] {
  display: none;
}

.request-mode [data-request-submit] {
  display: flex;
}

.request-mode [data-request-form] {
  display: block;
}

.request-mode [data-request-close] {
  display: block;
}

.request-mode [data-request-overlay] {
  display: block;
}

.request-mode [data-popup-close] {
  display: none;
}

.request-mode [data-popup-arrow] {
  display: block;
}

.container {
  max-width: 1250px;
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto;
  height: 100%;
}

body {
  // padding-top: 40px;
}

@media (min-width: 1025px) {
  .fullpage-wrapper {
    .section {
      height: 100vh;
    }
  }
}



