.services {
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    padding-left: pxToVw(200);
}

.services_special {
    padding-left: pxToVw(250);
    padding-bottom: pxToVw(0);
    position: relative;
}

.services__heading {
    font-family: "Charter", serif;
    font-size: pxToVw(90);
    line-height: pxToVw(80);
}

.services__heading.small {
    font-size: pxToVw(60);
    line-height: pxToVw(70);
}

.services__text {
    font-size: pxToVw(16);
    width: pxToVw(400);
    margin-top: pxToVw(40);
    line-height: pxToVw(24);
    font-weight: 300;
}
.services__links {
    font-size: pxToVw(14);
    margin-top: pxToVw(40);
}
.services__link {
    margin-right: pxToVw(80);
    cursor: pointer;
}
.services__link img {
    width: pxToVw(15);
    margin-left: pxToVw(15);
}

.services__wrap {
    display: flex;
    margin-top: 1vw;

    .form-validate-result {
        max-width: none;
        flex-grow: 1;
        align-self: center;
    }
}

.services__text._half {
    width: pxToVw(384);
    max-width: pxToVw(450);
    margin-right: pxToVw(30);
}

.services__form {
    margin-top: pxToVw(40);
    min-width: 45%;
    max-width: 45%;
    background: white;
    padding: pxToVw(20);

    border-radius: 16px;
   
}

.services__form._full {
    min-width: 70%;
    max-width: 70%;
}

.services__form._fixed {
    margin-top: pxToVw(20);
    width: pxToVw(800);
    max-width: pxToVw(550);
    padding: pxToVw(20);
}



.composition .services__form._fixed {
    width: 100%;
    max-width: 100%;
}

.services__form-text {
    color: #005090;
    font-size: pxToVw(15.36);
    margin-bottom: pxToVw(12.8);
    font-weight: 300;
}

.services__form._fixed .services__form-text {
    font-size: pxToVw(16);
    margin-bottom: pxToVw(27);
}

.services__form._fixed .full-menu__form-inputs {
    width: 100%;
    margin-bottom: pxToVw(20);
}

.services__form._fixed input {
    width: 50%;
    height: pxToVw(30);
    font-size: pxToVw(12);
    padding: pxToVw(1) pxToVw(2) pxToVw(5);
    padding-bottom: pxToVw(2);
    border-radius: 0;

    color: #67A1C4;
    &:nth-child(2) {
        width: 43%;
        @media (max-width: 1024px) {
            width: 100%;
        }
    }
    &::placeholder {
        color: #67A1C4;
    }
}

.services__form-required-message {
    font-size: pxToVw3(10);
    line-height: pxToVw3(11);

    margin-bottom: pxToVw3(25);
    padding-left: pxToVw3(20);
    color: #65A2C2;

    position: relative;
    &::before {
        content: "";
        width: 6px;
        height: 6px;

        background-color: red;
        border-radius: 50%;

        position: absolute;
        left: 0;
        top: 50%;

        transform: translateY(-50%);
        @media (max-width: 1200px) {
            width: 4px;
            height: 4px;
        }
    }
}

.services__form._fixed .full-menu__form-submit {
    font-size: pxToVw(11);
    margin-bottom: 0;
    margin-bottom: 0;
}

.services__form._fixed .full-menu__form-inputs.required-input-wrapper {
    position: relative;
    .required-input {
        padding-left: pxToVw(15); 
    }
    &::before {
        content: "";
        width: 7px;
        height: 7px;

        background-color: red;
        border-radius: 50%;

        position: absolute;
        left: 0;
        top: 50%;

        margin-top: pxToVw3(-2);

        transform: translateY(-50%);
        @media (max-width: 1200px) {
            width: 5px;
            height: 5px;
        }
    }
}

.services__form._fixed .full-menu__text3 {
    font-size: pxToVw(8);
}

.services__form._fixed .input::placeholder {
    font-size: pxToVw(12);
}

.services__breadcrumbs {
    position: absolute;
    top: pxToVw(45);
    left: pxToVw(250);
    font-family: "Charter", serif;
    font-weight: bold;
    font-size: pxToVw(16);
    z-index: 10;
}

.services__link_arrow img {
    width: pxToVw(100);
    padding-left: pxToVw(20);
}

.services__link_arrow {
    display: flex;
    align-items: center;
}

.services__heading_special {
    font-size: pxToVw(23);
    line-height: pxToVw(28);
    font-family: "Museo Sans", sans-serif;
    font-weight: 300;
    display: block;
    width: pxToVw(300);
}

.services__text_special {
    margin-top: pxToVw(10);
}

.services__big-text {
    font-family: "Charter", serif;
    font-size: pxToVw(90);
    line-height: pxToVw(80);
    margin-bottom: pxToVw(30);
    transform: translateX(pxToVw(-100));
}

.services__sale-icon img {
    width: pxToVw(150);
}

.services__inner_special {
    transition: transform 0.25s ease;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}
.services__slide {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    padding-left: 16.5vw;
    background-size: cover;
    transition: opacity 2s ease;
}

.services__slide-white {
    color: #005090;
    background-color: #fff;
}

.services__slide-container {
    position: absolute;
    min-width: pxToVw(960);
    top: 50%;
    transform: translateY(-50%);
}

.services__slide-container:not(:first-of-type) {
    opacity: 0;
}

.services__slider-controls {
    position: absolute;
    bottom: pxToVw(30);
    left: 50%;
    transform: translateX(-50%);
    align-items: center;
    display: none;
    height: pxToVw(20);
    z-index: 1;
}
.services__slider-left-arrow-cont {
    cursor: pointer;
    height: pxToVw(20);
    display: flex;
    line-height: pxToVw(20);

    &.blue {
        img {
            display: none;
        }

        svg {
            display: block;
        }
    }
}
.services__slider-left-arrow-cont img {
    width: pxToVw(24);
}

.services__slider-left-arrow-cont svg {
    width: pxToVw(24);
    display: none;
}
.services__slider-left-arrow-cont img:nth-child(2) {
    display: none;
}
.services__slider-left-arrow-cont img:nth-child(2) {
    transform: rotateZ(180deg);
}

.services__slider-left-arrow-cont svg {
    transform: rotateZ(180deg);
}

.services__slider-left-arrow-cont.active img:nth-child(1) {
    display: none;
}
.services__slider-left-arrow-cont.active img:nth-child(2) {
    display: block;
}

.services__slider-left-arrow-cont.active.blue img:nth-child(2) {
    display: none;
}

.services__slider-left-arrow {
}
.services__slider-dots {
    display: flex;
    padding: 0 pxToVw(10);

    &.blue {
        .services__slider-dot {
            border-color: #005090;

            &.active {
                background-color: #005090;
            }
        }
    }
}
.services__slider-dot {
    width: pxToVw(7);
    height: pxToVw(7);
    border: 1px solid white;
    border-radius: 50%;
    margin: 0 pxToVw(5);
    cursor: pointer;

    transition: all 0.2s ease-in-out;
}
.services__slider-dot.active {
    background: white;
}
.services__slider-right-arrow-cont {
    cursor: pointer;
    height: pxToVw(20);
    display: flex;
    line-height: pxToVw(20);

    &.blue {
        img {
            display: none;
        }

        svg {
            display: block;
        }
    }
}
.services__slider-right-arrow-cont img {
    width: pxToVw(24);
}

.services__slider-right-arrow-cont svg {
    width: pxToVw(24);
    display: none;
}

.services__slider-right-arrow-cont img:nth-child(1) {
    transform: rotateZ(180deg);
}

.services__slider-right-arrow-cont svg {
}

.services__slider-right-arrow-cont img:nth-child(2) {
    display: none;
}

.services__slider-right-arrow-cont.active img:nth-child(1) {
    display: none;
}

.services__slider-right-arrow-cont.active img:nth-child(2) {
    display: block;
}

.services__slider-right-arrow-cont.active.blue img:nth-child(2) {
    display: none;
}

.services__sale-icon {
    // position: absolute;
    // top: pxToVw(-166.4);
}

.services__sale-icon-text {
    position: absolute;
    top: pxToVw(40);
    left: pxToVw(40);
    font-size: pxToVw(60);
    font-weight: 100;
}

.services {
   
}

.input::-webkit-inner-spin-button,
.input::-webkit-calendar-picker-indicator {
    display: none;
    -webkit-appearance: none;
}


